function CardImage({ imgSrc, alt, className, wrapperClassName }) {
	return (
		<div className={`w-auto overflow-hidden ${wrapperClassName || ''}`}>
			<img src={imgSrc} alt={alt} className={`relative w-full bg-center object-cover ${className || ''}`} />
		</div>
	);
}

function CardBody({ children, className }) {
	return <div className={`p-4 md:p-5 ${className || ''}`}>{children}</div>;
}

function CardTitle({ children, className }) {
	return (
		<h3
			className={`mb-2 text-lg font-bold tracking-widest transition-all duration-500 ease-in-out group-hover:text-nespresso-gold max-lg:text-nespresso-gold lg:text-black ${
				className || ''
			}}`}
		>
			{children}
		</h3>
	);
}

function Card(props) {
	const { children, className, href, loading } = props;
	const loadingClasses = `
	relative
	invisible
	after:rounded-lg
	after:bg-gray-200
	after:absolute
	after:inset-0
	after:animate-pulse
	after:overflow-hidden
	after:animate-fast
	after:visible
	`;

	const classes = [
		loading
			? loadingClasses
			: `${href ? 'no-underline text-gray-800' : ''}
			bg-white
			rounded-lg
			overflow-hidden
			border
			border-gray-200
			transition-all
			duration-200
			ease-in-out
			shadow-lg
			group
			${className}
		`,
	];

	if (href) {
		return (
			<a href={href} className={classes}>
				{children}
			</a>
		);
	} else {
		return <div className={classes}>{children}</div>;
	}
}

Card.Image = CardImage;
Card.Body = CardBody;
Card.Title = CardTitle;
export default Card;
